export default function (carouselElt) {
  let cardsElt  = carouselElt.querySelector('.cards');
  let cardElts  = carouselElt.querySelectorAll('.card');
  let nextElt   = document.createElement('div');
  let prevElt   = document.createElement('div');
  let position  = 0;
  let cardCount = cardElts.length;

  nextElt.className = 'next';
  prevElt.className = 'prev';

  cardsElt.style.width = `${cardCount*100}%`;
  Array.prototype.forEach
  .call(cardElts, (cardElt, index) => {
    cardElt.style.flexBasis = `${100/cardCount}%`;
    cardElt.addEventListener('click', e => {
      slide(index, true);
    })
  });

  nextElt.addEventListener('click', e => {
    e.preventDefault();
    slide(1);
  });

  prevElt.addEventListener('click', e => {
    e.preventDefault();
    slide(-1);
  });

  cardElts[position].classList.add('focus');
  carouselElt.appendChild(prevElt);
  carouselElt.appendChild(nextElt);
  carouselElt.classList.add('ready');

  function slide (by, to) {
    cardElts[position].classList.remove('focus');

    position = !!to ?
      by :
      (position + by + cardCount) % cardCount;
    let x = 100 - ((cardCount-position)/cardCount)*100;
    cardsElt.style.transform = `translateX(${-x}%)`;

    cardElts[position].classList.add('focus');
  }
}