import buildCarousel from '../method/buildCarousel.js';
import buildImage    from '../method/buildImage.js';



export default function ( _data, _elt ) {
  let data   = _data.data;
  let elt    = _elt || 'section';
  let cards  = data.cards;

  let title_txt  = data.title.trim();
  let teaser_txt = data.teaser.trim();

  let event_elt  = document.createElement(elt);
  let title_elt  = document.createElement('h4');
  let teaser_elt = document.createElement('p');

  event_elt.className  = 'event';
  title_elt.innerHTML  = title_txt;
  teaser_elt.innerHTML = teaser_txt;

  if (!!title_txt)  { event_elt.appendChild(title_elt); }
  if (!!teaser_txt) { event_elt.appendChild(teaser_elt); }

  cards.forEach(card => {
    if (card.datatype === 'image') {
      event_elt.appendChild( buildImage(card) );
    }

    if (card.datatype === 'carousel') {
      event_elt.appendChild( buildCarousel(card) );
    }
  });

  return event_elt;
}