import timelineData   from './data/timeline.js';

import setOrientation from './method/setOrientation.js';
import buildTimeline  from './method/buildTimeline.js';
import initCarousel   from './method/initCarousel.js';



document.body.appendChild( buildTimeline( timelineData ) );

Array.prototype.forEach.call(document.querySelectorAll('.carousel'), initCarousel);

setOrientation();
window.addEventListener('resize', setOrientation);
