export default function ( _data, _elt ) {
  let data   = _data.data;
  let elt    = _elt || 'section';

  let title_txt  = data.title.trim();
  let teaser_txt = data.teaser.trim();
  let imgSrc_txt = data.src;

  let card_elt    = document.createElement(elt);
  let title_elt   = document.createElement('h6');
  let figure_elt  = document.createElement('figure');
  let image_elt   = document.createElement('img');
  let teaser_elt  = document.createElement('p');

  card_elt.className   = 'card';
  title_elt.innerHTML  = title_txt || '&nbsp;';
  teaser_elt.innerHTML = teaser_txt || '&nbsp;';
  image_elt.src        = imgSrc_txt;

  figure_elt.appendChild(image_elt);
  card_elt.appendChild(title_elt);
  card_elt.appendChild(figure_elt);
  card_elt.appendChild(teaser_elt);

  return card_elt;
}