import buildImage    from '../method/buildImage.js';



export default function ( _data, _elt ) {
  let data   = _data.data;
  let elt    = _elt || 'section';
  let cards  = data.cards;

  let title_txt  = data.title.trim();
  let teaser_txt = data.teaser.trim();

  let carousel_elt = document.createElement(elt);
  let cards_elt    = document.createElement('ul');
  let title_elt    = document.createElement('h5');
  let teaser_elt   = document.createElement('p');

  carousel_elt.className = 'carousel';
  cards_elt.className    = 'cards';
  title_elt.innerHTML    = title_txt;
  teaser_elt.innerHTML   = teaser_txt;

  if (!!title_txt)  { carousel_elt.appendChild(title_elt); }
  if (!!teaser_txt) { carousel_elt.appendChild(teaser_elt); }
  carousel_elt.appendChild(cards_elt);

  cards.forEach(card => {
    cards_elt.appendChild( buildImage(card, 'li') );
  });

  return carousel_elt;
}