import buildEvent from '../method/buildEvent.js';



export default function ( _data, _elt ) {
  let data   = _data.data;
  let elt    = _elt || 'article';
  let events = data.events;

  let title_txt  = data.title.trim();
  let teaser_txt = data.teaser.trim();

  let timeline_elt = document.createElement(elt);
  let title_elt    = document.createElement('h2');
  let teaser_elt   = document.createElement('p');

  timeline_elt.className = 'timeline';
  title_elt.innerHTML = title_txt;
  teaser_elt.innerHTML = teaser_txt;

  if (!!title_txt)  { timeline_elt.appendChild(title_elt); }
  if (!!teaser_txt) { timeline_elt.appendChild(teaser_elt); }

  events.forEach(event =>{
    timeline_elt.appendChild( buildEvent( event ) );
  });

  return timeline_elt;
}